import html2canvas from "html2canvas";
import { QRCodeCanvas } from "qrcode.react";
import React, { useState } from "react";
import Footer from './components/Footer';
import { AiFillCopy, AiOutlineDownload } from "react-icons/ai";

function App() {
  const [qr, setqr] = useState("");
  const [url, seturl] = useState("");
  const [bgColor, setBgColor] = useState("white");

  const QrCodeDownload = async () => {
    const canvasElement = document.getElementById("canvas");
    const canvas = await html2canvas(canvasElement, {
      backgroundColor: null  // This ensures the background remains transparent
    });
    const imageDataUrl = canvas.toDataURL("image/png");
    
    if (imageDataUrl) {
      const a = document.createElement("a");
      a.download = "QrCode.png";
      a.href = imageDataUrl;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
    }
  };

  const QrCodeCopy = async () => {
	const canvas = await (
		await html2canvas(document.getElementById("canvas"))
	  ).toDataURL();
	  if (canvas) {
		  setqr(canvas);
		  navigator.clipboard.writeText(qr);
		}
  };

  return (
	<>
	<main>
	<div>
      <h1 style={{ width: "100%" }}>Generate your Qr Code</h1>
      <div class="mb-4">
        <label>Enter your Data</label>
        <input
          placeholder="Add Your Data for Qr Code generation"
          required
          style={{ marginTop: 10 }}
          type="text"
          onChange={(e) => seturl(e.target.value)}
        />
        <label>Select Color</label>
        <select onChange={(e) => setBgColor(e.target.value)} value={bgColor} style={{ marginTop: 10 }}>
          <option value="white">Black</option>
          <option value="black">White</option>
        </select>
		 <button onClick={() => QrCodeDownload()} class="DownloadButton">
          <AiOutlineDownload />
          Download
        </button>
        {/* <button onClick={() => QrCodeCopy()} class="CoppyButton">
          <AiFillCopy />
          Copy
        </button> */}
      </div>
	  </div>
      <article className={bgColor === "white" ? "card-black card" : "card-white card"}>
        <div id="canvas" className="qr-box">
          <QRCodeCanvas
            value={url ? url : "https://library.dctabudhabi.ae/"}
            size={300}
            bgColor={"transparent"}
            fgColor={bgColor === "white" ? "#000000" : "#ffffff"}
            level={"H"}
            includeMargin={false}
            imageSettings={{
              src: bgColor === "white" ? "/Maktaba_Logo.png" : "/Maktaba_Logo_white.png",
              x: undefined,
              y: undefined,
              height: 39,
              width: 80,
              excavate: true,
            }}
          />
        </div>
      </article>
	</main>
	<Footer/>
</>
  );
}

export default App;
